import styled from 'styled-components';

interface RefBlockWrapperProps {
  isFocused?: boolean;
  $loading?: boolean;
  isHost?: boolean;
}

function getBorderColor(isHost?: boolean) {
  return isHost ? '#FF642B' : 'rgba(0, 137, 255, 0.48)';
}

export const RefBlockWrapper = styled.div<RefBlockWrapperProps>`
  position: relative;
  padding: 4px;
  border-width: 1px;
  border-style: ${(props) =>
    props.isFocused || props.$loading ? 'dotted' : 'solid'};
  border-color: ${(props) =>
    props.isFocused || props.$loading
      ? getBorderColor(props.isHost)
      : 'transparent'};
  border-radius: 8px;
  background-color: ${(props) => (props.$loading ? '#f3f3f378' : 'inherit')};
  ${(props) =>
    props.$loading ? 'animation: 1s animateBorderOne ease infinite;' : ''}

  @keyframes animateBorderOne {
    to {
      border-color: transparent;
    }
  }

  :hover {
    border-style: dotted;
    border-color: ${(props) => getBorderColor(props.isHost)};
  }

  .refblock-content {
    > div:first-child {
      margin-top: 0px !important;
    }

    > div:last-child {
      margin-bottom: 0px !important;
    }
  }
`;

